.slideshow {
  position: absolute;
  top: 0;
  left: 0;
  height: 700px;
  width: 100%;
}

.overlay {
  position: absolute;
  width: 100%;
  height: 100%;
  background: rgba(0,0,0,0.2);
}

.info {
  position: absolute;
  left: 20px;
  bottom: 20px;
  color: #f5f6f7;
  font-size: 30px;
  font-family: 'Poppins', sans-serif;
  padding: 10px;
}

.App {
  position: absolute;
  top: 0;
  height: auto;
  width: 100%;
}

.top {
  width: 100%;
  height: auto;
  color: #f5f6f7;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  z-index: 99;
}

.logo {
  position: absolute;
  font-size: 60px;
  background: #f5f6f7;
  margin-left: 20px;
  margin-top: 20px;
  padding: 10px;
  font-family: 'Kaushan Script', cursive;
  font-weight: bolder;
  color: #20480b;
  border: 1px solid #20480b;
  border-radius: 10px;
}

nav {
  position: fixed;
  right: 0;
  z-index: 99;
  display: flex;
  align-items: center;
  height: 50px;
  font-size: 20px;
  font-family: 'Poppins', sans-serif;
  font-weight: bolder;
  color: #20480b;
  background: #f5f6f7;
  border: 1px solid #20480b;
}

nav p {
  cursor: pointer;
  margin: 20px;
}

nav p:hover {
  text-decoration: underline #20480b;
}

header {
  margin-top: 200px;
  margin-bottom: 200px;
  width: 100%;
  color: #f5f6f7;
  text-align: center;
}

header h1 {
  font-size: 80px;
  font-family: 'Kaushan Script', cursive;
}

.main {
  margin-bottom: 150px;
  width: 100%;
  height: auto;
  display: flex;
  justify-content: center;
  align-items: center;
}

form {
  font-size: 30px;
  font-family: 'Poppins', sans-serif;
  margin-right: 100px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

input {
  height: 30px;
  width: 300px;
  margin-top: 20px;
  border: 1px solid #20480b;
  outline: none;
  padding: 10px
}

.arrow {
  color: #20480b;
  cursor: pointer;
  font-size: 30px;
  border: none;
}

.logreg {
  display: flex;
  width: 60%;
  flex-flow: wrap;
  justify-content: center;
  margin: auto;
  margin-top: 150px;
  margin-bottom: 150px;
}

.contributo {
  display: flex;
  width: 60%;
  flex-flow: wrap;
  justify-content: center;
  margin: auto;
  margin-top: 150px;
  margin-bottom: 150px;
}

.contributo p {
  margin-bottom: 20px;
  font-size: 25px;
  font-family: 'Poppins', sans-serif;
  text-align: center;
}

h2 {
  width: 100%;
  text-align: center;
  font-size: 40px;
  font-weight: lighter;
  color: #20480b;
  font-family: 'Kaushan Script', cursive;
  margin-bottom: 20px;
}

.logreg p {
  margin-bottom: 20px;
  font-size: 25px;
  font-family: 'Poppins', sans-serif;
  text-align: center;
}

button {
  padding: 7px;
  font-size: 30px;
  color: #20480b;
  background-color: #f5f6f7;
  border: 1px solid #20480b;
  border-radius: 5px;
  font-family: 'Poppins', sans-serif;
  cursor: pointer;
  transition: .3s ease;
}

button:hover {
  background-color: #20480b;
  color: #f5f6f7;
}

.logreg button:nth-child(3) {
  margin-right: 30px;
}

footer {
  color: #20480b;
  width: 100%;
  height: auto;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  font-size: 20px;
  font-family: 'Poppins', sans-serif;
  border-top: 1px solid #20480b;
}
